.select-ls {
  position: relative;
  font-family: Figtree, sans-serif;

  &__list {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 16px;
    margin: 0;
    border: none;
    border-radius: 3px;
    position: absolute;
    background-color: transparent;
    transition: box-shadow 80ms ease;
    font-size: inherit;
    border-right: 10px solid transparent;
  }

  &_ghost {
    margin-left: -10px;
    border: 1px solid transparent;

    &__list {
      padding: 0 5px;
    }

    &:hover {
      border: 1px solid var(--sand_300);
    }
  }
}

select:disabled {
  background: var(--sand_200);
  color: var(--sand_500);
  font-family: Figtree, sans-serif;
  opacity: 1;
}
